@import 'Header.scss';

.landing-page {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;

  .content {
    height: 200vh;
    position: relative;
  }

  .fade-section {
    background-color: #333;
    background-position: center;
    background-size: cover;
    color: #333;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
    z-index: 0;
    box-shadow: inset 0 -80px 90px black;

    &.visible {
      opacity: 1;
      transform: translateY(0);
      z-index: 1;
    }

    .background-video {
      position: absolute;
      opacity: 0.8;
      min-width: 100%;
      min-height: 100%;
      &.fit {
        min-width: 0;
        min-height: 0;
        max-width: 100%;
      }
    }

    h3 {
      color: white;
      font-size: 2rem;
      margin-bottom: 20px;
      text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
      z-index: 2;
      transition: all 0.5s ease-in-out;
      text-align: center;
    }
  }
}

.underline-anim {
  position: relative;
  overflow: hidden;
  z-index: 5;

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: white;
    transition: opacity 1s, transform 300ms;
    opacity: 1;
    transform: translate3d(-110%, 0, 0);
  }

  &:hover::after, :focus::after {
    transform: translate3d(0, 0, 0);
  }
} 

@keyframes fadeMove {
  0% {
    opacity: 0;
    transform: translate(-50%, 0);
  }
  10% {
    opacity: 1;
    transform: translate(-50%, 0px);
  }
  80% {
    opacity: 1;
    transform: translate(-50%, 35px);
  }
  100% {
    opacity: 0;
    transform: translate(-50%, 40px);
  }
}

