.landing-nav {
  position: fixed;
  bottom: 30px;
  z-index: 3;
  width: 1100px;
  text-align: center;
  display: flex;
  justify-content: center;

  p {
    font-size: 30px;
    color: white;
    margin: 0 10px 0 10px;
  }

  .nav-button {
    background: none;
    border: none;
    margin: 0 10px 0 10px;
    font-size: 30px;
    color: white;
    cursor: pointer;
    transition: all 0.2s;
    &:hover {
      transform: scale(1.05);
    }
  }
}
@media (max-width: 1100px) {
  .landing-nav {display: none;}
}
@media (max-height: 600px) {
  .landing-nav {display: none;}
}